import React,{useEffect,useState,useLayoutEffect} from 'react';
import {Paper} from '@material-ui/core';


const Header = ()=> {
    const [isSideMenuOpen, setIsSideMenuOpen] = useState(false);
    const [windowSize, setWindowSize] = useState(0);
    const handleClick = ()=>{
        setIsSideMenuOpen(!isSideMenuOpen);
        }
    useLayoutEffect(() => {
       window.addEventListener('resize',()=>setWindowSize(window.innerWidth));
    });

    useEffect(()=>{
         window.addEventListener('scroll',()=>{
             if(isSideMenuOpen){
                 setIsSideMenuOpen(!isSideMenuOpen);
             }
         });
      });
 
        return(
            <Paper elevation={6} id="header">
                <span id="leftPart"> <span className="letter">A</span>bhinav <span className="letter">J</span>aiswal</span>
                <span id="rightPartMobile" onClick={handleClick}>{!isSideMenuOpen?<span><hr/><hr/><hr/></span>:<span id='closeSideMenu'>✖</span>} </span>
                <div  id="sideMenu" style={isSideMenuOpen && windowSize<=785?{"display":"block"}:{"display":"none"}}>
                <a href="index.html"> <p onClick={()=>setIsSideMenuOpen(!isSideMenuOpen)}>Home</p></a>
                <a href="#projects">  <p onClick={()=>setIsSideMenuOpen(!isSideMenuOpen)}>Projects</p></a>
                    <a href="#skills"><p onClick={()=>setIsSideMenuOpen(!isSideMenuOpen)}>Skills</p></a>
                    <a href="#about"> <p onClick={()=>setIsSideMenuOpen(!isSideMenuOpen)}>About</p></a>
                   <a href="#contacts"> <p onClick={()=>setIsSideMenuOpen(!isSideMenuOpen)}>Contact</p></a>
                </div>
                <span id="rightPart">
                <a href="index.html">Home</a>
                <a href="#projects">Projects</a>
                <a href="#skills">Skills</a>
                <a href="#about">About</a>
                <a href="#contacts">Contact</a>
                </span>
           </Paper> 
        );
  
}
export default Header;


