import React,{useEffect,useState} from 'react';
import Projects from './projects';
import About from './about';
import Skills from './skills';
import Contact from './contact';

const Home = ()=>{
    const headingText = "Hello,My Name is Abhinav Jaiswal"
    const [firsth1, setFirsth1] = useState("");
    const [secondh1, setSecondh1] = useState("");
    const [thirdh1, setThirdh1] = useState("");
    
    useEffect(() => {
    let i = 0;
    function writeText(){
        if(i<headingText.length){
                if(i>=0 && i<=5){
                    setFirsth1((prev)=> prev + headingText[i++]);
                }
                else if(i<=16){
                    setSecondh1((prev)=> prev + headingText[i++]);
                }
                else{
                    setThirdh1((prev)=> prev + headingText[i++]);
                }
                setTimeout(()=>writeText(),100);
        }
        else{
               return null;
        }
           
       }
       writeText();
 

    },[]);
    return <React.Fragment>
    
        <section id="hero">
            <div className="hero container">
            <div >
                <h1>{firsth1}<br/>{secondh1}<br/><span id="heading3">{thirdh1}</span></h1>
                <a className="downloadButton" href="resume-abhinav.pdf">Download Resume</a>
                
            </div>
            </div>
        </section>
        <div id="follow">
        <a className="social" target="_blank"  rel="noopener noreferrer" href="http://www.github.io"><i className="fa fa-github"></i></a>
            <a className="social" target="_blank" rel="noopener noreferrer" href="http://www.linkedin.com/in/"><i className="fa fa-linkedin"></i></a>
            <a className="social" target="_blank" rel="noopener noreferrer" href="http://www.twitter.com"><i className="fa fa-twitter"></i></a>
            <a className="social" target="_blank" rel="noopener noreferrer" href="http://www.facebook.com"><i className="fa fa-facebook"></i></a>
        </div>
        <About/>
        <Skills/>
        <Projects/>
        <Contact/>
    </React.Fragment>
}

export default Home;